.page-header {
  position: relative;
  min-width: 320px;
  background-color: $light-blue;
  margin: 0 auto;
  text-align: center;

  @media (min-width: $tablet-width) {
    background-color: $white;
  }
}

.page-header__logo {
  position: absolute;
  bottom: -85px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 2;

  @media (min-width: $tablet-width) {
    top: 0;
  }
}

.page-header__logo-image {
  @media (min-width: $desktop-width) {
    width: 187px;
    height: 96px;
  }
}
