.post {
  @media (min-width: $desktop-width) {
    position: relative;
  }
}

.post--main {
  @media (min-width: $tablet-width) {
    width: 100%;
  }
}

.post--minor {
  @media (min-width: $tablet-width) {
    width: 50%;
  }

  @media (min-width: $desktop-width) {
    width: 33.333%;
  }
}

.post__wrapper {
  padding-bottom: 16px;
  border-bottom: 1px solid $midle-grey;

  @media (min-width: $tablet-width) {
    display: flex;
    justify-content: space-between;
    border-bottom: none;
    padding-top: 15px;
    padding-bottom: 3px;
    background-color: $light-grey;
  }

  @media (min-width: $desktop-width) {
    position: absolute;
    bottom: 0;
    width: 100%;
    background-color: rgba($black, 0.3);
  }
}

.post__wrapper--road {
  @media (min-width: $tablet-width) {
    background-color: $post-grey;
  }

  @media (min-width: $desktop-width) {
    background-color: rgba($black, 0.3);
  }
}

.post__image {
  margin-top: 22px;
  padding-left: 20px;

  @media (min-width: $tablet-width) {
    padding-left: 0;
    margin-top: 0;
    width: 324px;
    height: 267px;
    display: block;
  }

  @media (min-width: $desktop-width) {
    width: 400px;
  }
}

.post__image--main {
  @media (min-width: $tablet-width) {
    width: 648px;
    height: 400px;
  }

  @media (min-width: $desktop-width) {
    width: 1200px;
  }
}

.post__title {
  font-weight: 700;
  font-size: 14px;
  line-height: 24px;
  margin-top: 4px;
  margin-bottom: 2px;
  padding-left: 20px;

  @media (min-width: $tablet-width) {
    margin-top: 0;
  }

  @media (min-width: $desktop-width) {
    color: $white;
  }
}

.post__author {
  line-height: 24px;
  text-transform: none;
  color: $dark;
  padding-left: 20px;
  margin-top: 0;
  margin-bottom: 12px;

  @media (min-width: $desktop-width) {
    color: $white;
  }
}

.post__like {
  display: flex;
  padding-left: 20px;
}

.post__icon {
  fill: rgba($black, 0.3);

  @media (min-width: $tablet-width) {
    padding-top: 13px;
  }

  @media (min-width: $desktop-width) {
    fill: $white;
  }
}

.post__like-number {
  font-weight: 700;
  line-height: 24px;
  margin-top: 0;
  margin-left: 14px;
  position: relative;

  @media (min-width: $tablet-width) {
    padding-left: 0;
    padding-right: 30px;
    padding-top: 14px;
    margin-left: 8px;
  }

  @media (min-width: $desktop-width) {
    color: $white;
  }
}

.post__like-number--main {
  @media (min-width: $tablet-width) {
    padding-right: 20px;
  }
}
